import React from "react"
import { StaticImage } from "gatsby-plugin-image"

interface heroProps {
    category: Queries.Maybe<string>
}

const Hero = ({ category }: heroProps) => {
    var retval = (category == "All" || category == undefined) ? 
    <StaticImage src="../images/hero.png" alt="" layout="fullWidth"/> : null
    return (retval)
}

export default Hero